import React, { useState } from 'react';
import {Link} from 'gatsby';

const Scores = ({id, datasource} : {id:string, datasource:any}) => { 
    const JSONData = datasource;
    const [currentWeek, setCurrentWeek] = useState(0);

    const formatMatchDate = (datetime:string) => {
        var d = new Date(datetime);
        return d.toUTCString();
    };

    const showWinInfo = (datetime:string) => {
        var d = new Date(datetime);
        const today = new Date();
        return d > today;
    };

    const nextWeek = () => {
        if((currentWeek + 1) < JSONData.schedules.length) {
            setCurrentWeek(currentWeek + 1);
        }
        debugger;
    };

    const prevWeek = () => {
        if((currentWeek - 1) >= 0) {
            setCurrentWeek(currentWeek - 1);
        }
        debugger;
    };

    const PageLayout = <section className="tf-section tf-item nopadding">
        <div className="container">
            <div className="tournament-section-header">
                <h6>
                    <a href="#" onClick={nextWeek} className={currentWeek < JSONData.schedules.length - 1 ? "" : "hide"}>&laquo;</a>&nbsp;
                    Scores - Week {JSONData.schedules[currentWeek].week}&nbsp;
                    <a href="#" onClick={prevWeek} className={currentWeek > 0 ? "" : "hide"}>&raquo;</a>
                </h6>
            </div>
            <div className="row">                
                <div className="col-md-12 center">  
                    {JSONData.schedules[currentWeek].matches.map((match:any, index:number) => (
                        <div className="tf-match" key={index}>
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="matchDateTime">{formatMatchDate(match.datetime)}</div>
                                </div>
                            </div>
                            <div className="match row">
                                <div className="col-md-5">
                                <Link to={"players/" + match.homecaptain ? match.homecaptain : ""} className="matchTeam"><span>{match.home}</span></Link>
                                </div>
                                <div className="col-md-2 matchResult">
                                    <span className="matchScore">{match.homescore}</span> - <span className="matchScore">{match.outsidescore}</span>
                                </div>
                                <div className="col-md-5">
                                    <Link to={"players/" + match.outsidecaptain ? match.outsidecaptain : ""} className="matchTeam"><span>{match.outside}</span></Link>
                                </div>
                            </div>
                            <div className={showWinInfo(match.datetime) ? "row matchInfo" : "hide"}>
                                <div className="col-md-12 center">
                                    <Link to="/claim" className={match.homescore != match.outsidescore ? "tf-button btn-effect" : "hide"}>
                                        <span className="boder-fade"></span>                                     
                                        <span className="effect">
                                            All "{match.homescore > match.outsidescore ? match.home : match.outside}" C-Shirts holders can claim their win
                                        </span>
                                    </Link>
                                </div>
                            </div>                           
                        </div>
                    ))}  
                    <div className={JSONData.schedules[0].matches.length != 0 ? "hide": ""}>No data available</div>                
                </div>
            </div>
        </div>
    </section>
    return(
        PageLayout
    );
};

export default Scores;

